import React, {useState} from 'react';
import { useLocation, useHistory} from "react-router-dom";
import useGetFromDb from '../../hooks/useGetFromDb';
import useGetHtml from '../../hooks/useGetHtml';

export default function AspectEdit({aspectId, aspectNameEdit, aspectDescr, aspectFilters, saveFilters, chosenFeatures, setChosenFeatures, handleAddCombi, handleDeleteCombi, groupValues, setGroupValues}) {

    const [formValue1, setFormValue1] = useState(null)
    const [formValue2, setFormValue2] = useState(null)
    const location = useLocation();
    const dceFeatures = location.dceFeatures;
    const groupFeature = location.groupFeature;
    const history = useHistory();

    if(groupFeature===undefined){
        history.push('/');
    }
   
    const handleFName = (fName, cIndex)=>{
        // e.preventDefault();
        setFormValue1(null);
        // console.log("SetFormValue null");
        // const { value, id} = e.target;
        // const cIndex = e.target.dataset.id;
        // console.log("handleChosen triggered, target:"+JSON.stringify(e.target.dataset));
        // console.log("value is: "+JSON.stringify(value));
        // console.log("chosenFeatures before copy: "+JSON.stringify(chosenFeatures));
        // console.log("aspectFilter before copy: "+JSON.stringify(aspectFilters));
        const chosenList = [...chosenFeatures];
        // chosenList[0]['fName'] = value;
        chosenList[cIndex][0]={fName: fName};
        // chosenList.push({fName: value});
        setChosenFeatures(chosenList);
    }

    const handleFValue = (fValue, cIndex) =>{
        // e.preventDefault();
        setFormValue1(null);
        setFormValue2(null);
        // const {value, id } = e.target;
        // const cIndex = e.target.dataset.id;
        const chosenList = [...chosenFeatures];
        chosenList[cIndex][0]['fValue'] = fValue;
        setChosenFeatures(chosenList);
    }
    
    const handleGroupValue = (value, cIndex)=>{
        // e.preventDefault();
        // const {value, id } = e.target;
        // const cIndex = e.target.dataset.id;
        let tempGValues = [...groupValues]
        tempGValues[cIndex]=value
        setGroupValues(tempGValues);
        }
 

    const handleSave = (e)=>{
        e.preventDefault();
        const cIndex = e.target.dataset.id;
        let tempList = [...aspectFilters[cIndex],...chosenFeatures[cIndex]];
        let tempChosen = [...chosenFeatures];
        tempChosen[cIndex] = [];
        console.log('group values: '+JSON.stringify(groupValues))
        if (groupValues[cIndex].length===0){
            alert("Select a value for the Group feature before saving")
        } else{
        saveFilters(cIndex, tempList, tempChosen, groupValues);
        console.log("tempList in save: "+JSON.stringify(tempList))
        console.log("tempChosen in save: "+JSON.stringify(tempChosen))
        // setChosenFeatures(tempChosen);
        setFormValue1("");
        console.log("SetFormValue blanc");
        setFormValue2("");
        }
    }

    const handleDelete = (e, index, cIndex)=>{
        e.preventDefault();
        let tempCIndex = cIndex.toString();
        let tempFilters = [...aspectFilters[tempCIndex]];
        let emptyChosen = [];
        aspectFilters.map(x=>{emptyChosen.push([])});
        tempFilters.splice(index, 1);
        saveFilters(tempCIndex, tempFilters, emptyChosen, groupValues);
    }

    const {fromDb} = useGetFromDb(aspectId, chosenFeatures, groupValues, aspectFilters, handleDeleteCombi, dceFeatures, handleDelete, handleGroupValue, groupFeature, handleFName, handleFValue, handleSave, formValue1, formValue2);  
    const html = useGetHtml(chosenFeatures, aspectNameEdit, formValue1, dceFeatures, formValue2, handleFValue, handleSave, handleFName )


let addCombiButton = [];
   if(aspectNameEdit!=='(Click on ICON in Aspect list)'){
       addCombiButton.push(
        <button className="btn btn-sm btn-outline-info my-4" onClick={handleAddCombi}>add combination feature</button>
        )
}

    return (
        <div>
            <h4 className="mb-1">Define aspect for <b>{aspectNameEdit}</b></h4>
            <h6 className="text-secondary pb-3">Description: {aspectDescr}</h6>
             
              {fromDb}
              {fromDb.length===0&&html}
              {addCombiButton}
        </div>
    )
}
