import React, { useState, useEffect } from "react";

export default function useGetHtml(chosenFeatures, aspectNameEdit, formValue1, dceFeatures, formValue2, handleFValue, handleSave,handleFName ) {
    const [html, setHtml] = useState([]);

    useEffect(() => {
        console.log("Rerender choose html");
        console.log("chosenFeatures in choose html: "+JSON.stringify(chosenFeatures));
        // setFormValue2("");
        let fV2="";
        let tempHtml = [];
        if(aspectNameEdit!=='(Click on ICON in Aspect list)'){
            tempHtml.push(
                <div>
                <form>
                  <label htmlFor='fNames' className="col-md-3 col-form-label">Add filter feature
                      <select className="form-select form-select-sm col-md-3  mb-3" defaultValue="" value={formValue1} data-id={0} id={chosenFeatures.length-1} onChange={handleFName}>
                        <option value="" disabled></option>
                          {dceFeatures&&dceFeatures.map((feature,ind) =>{
                              console.log("ind is: "+ind)
                              return (
                                <option value={feature.featureName}>{feature.featureName} </option>
                              )
                          })}
                      </select>
                  </label>
                  </form>
                  {dceFeatures&&<form>
                  <label htmlFor="fValue">Filter value
    <select name="fValue" id="" className="form-select form-select-sm col-md-3  mb-3" defaultValue="" value={formValue2} data-id={0} onChange={handleFValue}>
    <option value="" disabled></option>
    {console.log("i="+chosenFeatures.length-1)}
    {console.log("chosenFeatures: "+JSON.stringify(chosenFeatures))}
    {chosenFeatures[0][0]&&<option value={dceFeatures.find(x=>x.featureName===chosenFeatures[0][0]["fName"]).featureValue1}>{dceFeatures.find(x=>x.featureName===chosenFeatures[0][0]["fName"]).featureValue1}</option>}
    {chosenFeatures[0][0]&&<option value={dceFeatures.find(x=>x.featureName===chosenFeatures[0][0]["fName"]).featureValue2}>{dceFeatures.find(x=>x.featureName===chosenFeatures[0][0]["fName"]).featureValue2}</option>}
    </select>
                  </label>
                  </form>}
                  <button data-id={'0'} className="btn btn-sm btn-outline-info mb-3" onClick={handleSave}>save filter feature</button>
    
                  </div>
            )}
            setHtml(tempHtml);
    
        return 
    }, [chosenFeatures])
    
    return html;
}
