import './App.css';
import Fab from './Fab';
import MenuTabs from './MenuTabs';
import Modals from './Modals';
import M from 'materialize-css/dist/js/materialize.min.js';
import PcView from './PcView';
import { useState, useEffect, useRef } from "react";
import {  HashRouter as Router, Switch, useLocation, useParams} from "react-router-dom";
import EndForm from './EndForm';
import { pemFirestore } from '../../firebase/config';
import useFirestoreScenarios from '../../hooks/useFirestoreScenarios';


function App() {


  const [answerList, setAnswerList] =useState([]);
  const [qIndex, setQIndex] = useState(1);
  const [done,setDone] = useState([<span class='dot blue darken-2'></span>]);
  const [todo, setTodo] = useState([]);
  const [sc1, setSc1] = useState(null);
  const [sc2, setSc2] = useState(null);
  const [prevRandomA, setPrevRandomA] = useState(null);
  const [prevRandomB, setPrevRandomB] = useState(null);
  const [nrQuestions, setNrQuestions] = useState(0);
  const [isGrouped, setIsGrouped] = useState(false);
  const [dceDescr, setDceDescr] = useState(null);
  const [dceFeatures, setDceFeatures] = useState([]);
  const [dceDetails, setDceDetails] = useState(null)
  const [dceNameEdit, setDceNameEdit] = useState('');
  const [finished, setFinished] = useState(false);
  const {userId, dceId } = useParams();
  const {scenarios} = useFirestoreScenarios(userId, dceId);
  const initialRender = useRef(true);

useEffect(() => {
  //initialize materialize - css library
  M.AutoInit();
});


// handle click from choose button
  const handleClick = (choiceValue) => {
    let tempListA =[...answerList];
    tempListA.push(choiceValue);
    setAnswerList(tempListA);
    setScenarios(isGrouped);
    //finishes if nr of questions as defined in Dce is reached
    if(qIndex+1>nrQuestions){
      setFinished(true);
    }
    setQIndex(qIndex + 1);
    calcDots(); 
   }



   //calculate nr of dots of questions still to answer
    const calcDots = ()=>{
        //indicator dots for number of questions
        let dones = [];
        dones = [...done];
        dones.push(<span class="dot blue darken-2"></span>);
        setDone(dones);
        //indicator dots for questions still to answer
        let todos = [];
        todos = [...todo];
        todos.pop();
        setTodo(todos);
    }

//Sets the dots that indicate the questions that have to be answered
const setDots = (length)=>{
  let dot = [];
  for(let i=0; i<length-1;i++){
  dot.push(<span class="dot"></span>)
  };
  setTodo(dot);
return;
};

// set the 2 scenario's that are shown on the screen. Either random or grouped.
const setScenarios = (tempGrouped)=>{
  if(!tempGrouped){
    const {ScenarioA, ScenarioB}  =  getRandomScenarios();
    setSc1(ScenarioA);
    setSc2(ScenarioB);
   }
  if (tempGrouped){
    const {ScenarioA, ScenarioB} = getGroupedScenarios();
    setSc1(ScenarioA);
    setSc2(ScenarioB);
    }
 }

 
//Get 2 scenarios that are only differing in the group feature. Scenario A is random
//Scenario B is the pair of A
const getGroupedScenarios = ()=>{
  const maxNr = scenarios.length;
  let randNrA = getRandomANr(maxNr);
  const scenarioA = scenarios[randNrA];
  //if there are not enough scenarios available, finish experiment
  if (!scenarioA){
    setFinished(true)
  }
  else{
    const altnr = scenarioA["data"]["altnr"];
    const scenarioB = scenarios.find(sc=>(sc["data"]["nr"]===altnr));
    return({ScenarioA: scenarioA, ScenarioB: scenarioB});  
  }
  return({ScenarioA: "", ScenarioB: ""});
}


//get 2 random scenarios, no repeats and A!=B
const getRandomScenarios = ()=>{
const {RandomNrA, RandomNrB} = getUnusedRndNrs();
return ({ScenarioA: scenarios[RandomNrA], ScenarioB: scenarios[RandomNrB]});
}


// get random nr for the first scenario and store used nrs to avoid repeats
const getRandomANr = (maxNr)=>{
  let usedNrsA = [];
  if (prevRandomA!=null){usedNrsA=[...prevRandomA]}
  let randNrA=-1;
  for (let i=scenarios.length*5; i>0; i--){
      const tempRandNrA = Math.floor(Math.random() * maxNr);
      if (!usedNrsA.includes(tempRandNrA)){
        randNrA=tempRandNrA;
        break;
        }
    }
  //store A-nr if it is used
  if(randNrA!==-1){usedNrsA.push(randNrA); }
  setPrevRandomA(usedNrsA);
  return randNrA;
}

//Gets 2 random nrs that have not been used before in this session where B is different from A
//Used for Dce's without grouped scenario's
const getUnusedRndNrs = ()=>{
      let usedNrsB = [];
      if (prevRandomB!=null){usedNrsB=[...prevRandomB]}
      const maxNr = scenarios.length;
      let randNrA = getRandomANr(maxNr);
      let randNrB=-1;
      for (let i=scenarios.length*10; i>0; i--){
        const tempRandNrB = Math.floor(Math.random() * maxNr);
        if (randNrA!==tempRandNrB){
          randNrB=tempRandNrB;
          break;
        }
      }
      if(randNrB!==-1){usedNrsB.push(randNrB);}
      setPrevRandomB(usedNrsB);
      return {RandomNrA: randNrA, RandomNrB: randNrB};
}



//load dce details from database
//execute at startup and upon change of scenarios
useEffect(()=>{
//get Dce
    let response = pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId);
    response.get()
    .then((doc=>{
        let tempGrouped = doc.data().grouped;
        setDceFeatures(doc.data().features);
        const tempDetails = {details: {grouped: doc.data().grouped, groupFeature: doc.data().groupFeature, nrQuestions: doc.data().nrQuestions}}
        setDceDetails(tempDetails);
        setDceDescr(doc.data().descr.dceDescr);
        setDceNameEdit(doc.data().name.dceName);
        setNrQuestions(doc.data().nrQuestions);
        setIsGrouped(tempGrouped);
        setDots(doc.data().nrQuestions);
        if (scenarios.length!==0){
          setScenarios(tempGrouped);
         }
      }))
  },[scenarios]);

//Save results to database when experiment is finished
useEffect(() => {
  // ensure this effect does not run on startup
  if (initialRender.current) {
    initialRender.current = false;
  } else {
 //save result to firstore
  let collRef = pemFirestore.collection('users').doc(userId).collection('DceList').doc(dceId).collection('RawResults')
  answerList.map((answer,i)=> {collRef.add(answer);
  });
}
}, [finished])


  return (
    <div className="App">
      <Modals  nrQuestions={nrQuestions} sc1={sc1} sc2={sc2}/>
      {!finished && <MenuTabs handleClick = {handleClick} sc1={sc1} sc2={sc2} dceDescr={dceDescr} />} 
      {!finished && sc1&&sc2&& <PcView  handleClick = {handleClick} sc1={sc1} sc2={sc2} dceDescr={dceDescr}/>}
      {finished&&userId&&dceId&& <EndForm dceDetails={dceDetails} dceNameEdit = {dceNameEdit} dceId={dceId} dceFeatures={dceFeatures} userId={userId}/>}
      {/* {!finished&&<div> {answerList && <p>Choicelist: {JSON.stringify(answerList)}</p>}
                </div>} */}
      {!finished&&<Fab/>}
      {!finished&& <div className="footer">
         {done}
         {todo}
      </div>}
    </div>
  )
}

export default App;
